/* Wrapper around switch */
.wrapper__switch {
  padding: 30px;
  background-color: white;

  --pillar--padding: 20px;
  --pillar__logo--width: 84px;
}

.wrapper__pillar {
  position: absolute;
  top: 0;
  left: calc(
    50% - calc(var(--pillar__logo--width) / 2) - var(--pillar--padding)
  );
}
.pillar {
  padding: var(--pillar--padding);
  padding-top: 60px;
  background-color: black;
}
.pillar__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}
.footer__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

/* classes to be used in components */
.component__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__background {
  width: 100%;
  background-color: black;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.5) 0%,
      rgba(0, 0, 0, 1) 75%
    ),
    url("/home/arnell/React/upgraded-umbrella/src/assets/images/bg.png");
  background-position: top;
  background-repeat: no-repeat;
  color: white;

  padding-top: 170px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__inner {
  /* margin: 0 20px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.component__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.component__title h2 {
  letter-spacing: 3.5px;
  margin-bottom: 10px;
  font-size: 30px;
}
.component__title hr {
  width: 80%;
  margin-bottom: 30px;
}
.component__footer {
}
.component__wrapper h1 {
  width: 80%;
  letter-spacing: 3.5px;
  text-transform: uppercase;
}
.component__spacer {
  height: 60px;
}
.component__spacer--small {
  height: 40px;
}
.component__answers {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.component__answer {
  padding: 20px 0;
  border-top: 3px solid #191919;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.component__answer--inner {
  width: 80%;
}
.component__text {
  font-size: 24px;
  width: 80%;
}
.component__text--end {
  font-weight: 700;
  text-align: center;
  letter-spacing: 1.5px;
}

/* RadioButton */
.RadioButton {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 80%;
  opacity: 1;
  transition: all 0.1s ease-in-out;
}
.RadioButton:active {
  opacity: 0;
  transition: all 0.1s ease-in-out;
}
.RadioButton__outer {
  border: 3px solid white;
  border-radius: 50%;
  padding: 8px;
  cursor: pointer;
  margin-bottom: 10px;
}
.RadioButton__outer:hover .RadioButton__inner {
  transform: scale(1);
  transition: all 0.12s ease-in-out;
}
.RadioButton__outer:active .RadioButton__inner {
  transform: scale(0.9);
  transition: all 0.12s ease-in-out;
}
.RadioButton__inner {
  background-color: white;
  border-radius: 50%;
  height: 32px;
  width: 32px;
  transform: scale(0);
  transition: all 0.12s ease-in-out;
}
.RadioButton__text {
  font-size: 35px;
  text-align: center;
}
