/* LinkButton */
.LinkButton {
  width: 80%;
  max-width: 300px;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 30px;
  border: 3px solid white;
  border-radius: 12px;

  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.LinkButton:hover {
  background-color: white;
  color: black;
  transition: all 0.2s ease-in-out;
}
.LinkButton h2 {
  letter-spacing: 5px;
  text-transform: uppercase;
}

/* LinkArrow */
.LinkArrow {
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  padding-bottom: 5px;
  cursor: pointer;

  border-bottom: 2px solid rgba(255, 255, 255, 0);
}
.LinkArrow:hover {
  border-bottom: 2px solid white;
  transition: all 0.2s ease-in-out;
}
.LinkArrow img {
  margin-left: 15px;
  height: auto;
  width: 24px;
}
.LinkArrow:hover img {
  transform: translateX(4px);
  transition: all 0.2s ease-in-out;
}
.LinkArrowBold {
}
.LinkArrowBold img {
  margin-left: 20px;
}
.LinkArrowBold h4 {
  font-size: 24px;
  text-align: right;
  letter-spacing: 2.5px;
}
.LinkArrowLight {
}
.LinkArrowLight h4 {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 4px;
}

/* ScrollDown */
@-webkit-keyframes ScrollDownArrow {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
}
@-moz-keyframes ScrollDownArrow {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
}
@-o-keyframes ScrollDownArrow {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
}
@keyframes ScrollDownArrow {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
}

.ScrollDown {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.ScrollDown p {
  font-size: larger;
  letter-spacing: 4px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.ScrollDown img {
  height: auto;
  width: 36px;
  -webkit-animation: ScrollDownArrow 3s infinite; /* Safari 4+ */
  -moz-animation: ScrollDownArrow 3s infinite; /* Fx 5+ */
  -o-animation: ScrollDownArrow 3s infinite; /* Opera 12+ */
  animation: ScrollDownArrow 3s infinite; /* IE 10+, Fx 29+ */
}
